.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
.hide{
  display:none;
}
.show{
  display:block;
}
#presentSubMenu, #futureSubMenu, #aaasubMenu{
  margin-left:12px;
  font-size: 14px;
  color:#5f5c5c;
}
.w3-teal, .w3-hover-teal:hover {
  color: #fff!important;
  background-color: #009688!important;
  height: 50px !important;
}

div.menuDetails ul{
  list-style: none;
  padding:0;
}

div.menuDetails ul li {
  padding:5px 10px;
  background-color: #e4e4e4 ;
  border: 1px solid #ffffff;
}
div.menuDetails{
  border:1px;
  padding:10px;
  box-shadow: 0px 7px 15px -3px #967979 !important;

}
main > div{
  margin:auto;
  max-width: 600px;
}
main{
  padding:70px 20px 0;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.w3-teal {
  color: #ffffff !important;
  background-color: #000000 !important;
  box-shadow: -2px 7px 9px -4px #967979 !important;
}
#closeButton {
  background-color: #afafaf;
}
.headerTitle{
  padding: 15px 20px 0;
  display: flex;
  flex-direction: column;
  height: 100%;
}
h5.eventHeader{
  color:#797979;
}
.spinnerEvent{
float:right;
}
.loginoutbtn{
  color:#ff5200;;
}
.row{
  margin-bottom: 12px;
}

#box {
  border: 1px solid #00b76b;;
  padding: 10px;
  box-shadow: 5px 10px 18px #888888;
}
.headline{
  padding: 12px;
    border-bottom-style: solid;
    border-bottom-color: #006d40;
    box-shadow: 0px 8px 8px -7px #024c45;
    color: #006951;
}
.waves-effect {
      color:purple;
  }
  .note-message{
    color: #a90000;
    font-size: 13px;
  }
  .menu-link{
    font-size: 17px;
    color: rgb(0, 105, 81);
    font-style: italic;
  }
